@font-face {
  font-family: "Ubuntu";
  src: local("Ubuntu"),
    url(https://fonts.googleapis.com/css2?family=Ubuntu:ital,wght@0,300;0,400;0,500;0,700;1,300;1,400;1,500;1,700&display=swap)
      format("woff");
}
.elementCard {
  position: absolute;
  display: none;
  opacity: 0;
  color: white;
  background: #023;
  border: black solid 2px;
  height: 85vh;
  width: 85vw;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 10000;
  border-radius: 5vh;
  font-family: "ubuntu", sans-serif;
}

.image {
  position: absolute;
  width: 25vh;
  height: 25vh;
  right: 4vw;
  top: 4vh;
  border-radius: 1rem;
}

.title {
  font-size: 4rem;
  text-align: center;
}
.info {
  left: 5%;
  width: 65vw;
  position: absolute;
  font-size: x-large;
  top: 30vh;
  white-space: pre-wrap;
}
