.cell {
  width: 50px;
  height: 50px;
  padding: 0;
  color: #f5f5ff;
  text-shadow: 0 0 5px rgba(200, 200, 240, 0.5);
  margin: 5px;
  border-radius: 5px;
  transition: ease 0.2s;
  z-index: 10;
}
.cell h2 {
  font-size: 19px;
  text-align: center;
  margin-top: -7px;
}
.cell h3 {
  font-size: 8px;
  text-align: center;
  margin-top: -18px;
  text-shadow: 0 0 2px rgba(200, 200, 240, 0.3);
}
.cell h4 {
  font-size: 8px;
  text-align: center;
  margin-top: -8px;
}
.cell span {
  font-size: 8px;
  margin: 0 3px;
}
.cell:hover {
  transform: scale(2);
  background-color: #023;
  z-index: 10;
  cursor: pointer;
}

.demo {
  width: 107px;
  height: 105px;
  padding: 0;
  color: #f5f5ff;
  text-shadow: 0 0 5px rgba(200, 200, 240, 0.5);
  box-shadow: 0 0 1px #fff;
  margin: 5px;
  border-radius: 5px;
  transition: ease 0.2s;
  z-index: 10;
}
.demo h2 {
  font-size: 30px;
  text-align: center;
  margin-top: 10px;
}
.demo h3 {
  font-size: 16px;
  text-align: center;
  margin-top: -20px;
  text-shadow: 0 0 2px rgba(200, 200, 240, 0.3);
}
.demo h4 {
  font-size: 12px;
  text-align: center;
  margin-top: -16px;
}
.demo span {
  font-size: 12px;
  margin: 0 3px;
}

.non-metals {
  box-shadow: 0 0 3px rgba(240, 40, 40, 0.5),
    inset 0 0 25px rgba(240, 40, 40, 0.5);
}
.non-metals:hover {
  box-shadow: 0 0 3px rgba(242, 64, 64, 0.5),
    inset 0 0 25px rgba(245, 112, 112, 0.5);
}

.alkalis {
  box-shadow: 0 0 3px rgba(60, 220, 240, 0.5),
    inset 0 0 25px rgba(60, 220, 240, 0.5);
}
.alkalis:hover {
  box-shadow: 0 0 3px rgba(84, 224, 242, 0.3),
    inset 0 0 25px rgba(131, 233, 245, 0.3);
}

.alkalines {
  box-shadow: 0 0 3px rgba(250, 80, 0, 0.5),
    inset 0 0 25px rgba(250, 80, 0, 0.5);
}
.alkalines:hover {
  box-shadow: 0 0 3px rgba(255, 96, 21, 0.5),
    inset 0 0 25px rgba(255, 165, 123, 0.5);
}

.transitions {
  box-shadow: 0 0 3px rgba(180, 180, 180, 0.5),
    inset 0 0 25px rgba(180, 180, 180, 0.5);
}
.transitions:hover {
  box-shadow: 0 0 3px rgba(193, 193, 193, 0.5),
    inset 0 0 25px rgba(255, 255, 255, 0.5);
}

.basic-metals {
  box-shadow: 0 0 3px rgba(0, 0, 0, 0.9), inset 0 0 25px rgba(0, 0, 0, 0.9);
}
.basic-metals:hover {
  box-shadow: 0 0 3px rgba(0, 0, 0, 0.7), inset 0 0 25px rgba(10, 10, 10, 0.7);
}

.semi-metals {
  box-shadow: 0 0 3px rgba(250, 250, 0, 0.4),
    inset 0 0 25px rgba(250, 250, 0, 0.4);
}
.semi-metals:hover {
  box-shadow: 0 0 3px rgba(255, 255, 21, 0.5),
    inset 0 0 25px rgba(255, 255, 72, 0.5);
}

.halogens {
  box-shadow: 0 0 3px rgba(0, 250, 0, 0.4), inset 0 0 25px rgba(0, 250, 0, 0.4);
}
.halogens:hover {
  box-shadow: 0 0 3px rgba(21, 255, 21, 0.5),
    inset 0 0 25px rgba(72, 255, 72, 0.5);
}

.noble-gas {
  box-shadow: 0 0 3px rgba(40, 150, 250, 0.4),
    inset 0 0 25px rgba(40, 150, 250, 0.4);
}
.noble-gas:hover {
  box-shadow: 0 0 3px rgba(65, 162, 251, 0.5),
    inset 0 0 25px rgba(115, 187, 252, 0.5);
}

.lanthanides {
  box-shadow: 0 0 3px rgba(240, 0, 250, 0.5),
    inset 0 0 25px rgba(240, 0, 250, 0.5);
}
.lanthanides:hover {
  box-shadow: 0 0 3px rgba(246, 21, 255, 0.5),
    inset 0 0 25px rgba(248, 72, 255, 0.5);
}

.actinides {
  box-shadow: 0 0 3px rgba(250, 80, 120, 0.5),
    inset 0 0 25px rgba(250, 80, 120, 0.5);
}
.actinides:hover {
  box-shadow: 0 0 3px rgba(251, 105, 139, 0.5),
    inset 0 0 25px rgba(252, 154, 177, 0.5);
}

.hidden:hover {
  transform: scale(1);
  cursor: default;
}
